import React, { Component } from "react";
import Comment from "./Comment";
import { inject, observer } from "mobx-react";
import { latinToPersian, convertMillisecondToString } from "../../util/util";
var moment = require("moment-jalaali");
import {MainUrl} from '../../util/RequestHandler'

@inject("session")
@observer
export default class MovieComment extends React.Component {
  constructor(props) {
    super(props);
    this.state = { comments: null, commentCount: 0 };
  }

  showLogin() {
    this.props.session.showLogin = true;
  }

  getComments() {
    $.ajax({
      type: "Get",
      url: MainUrl+"/comments.ashx?movieId=" + this.props.movieId,
      success: function(data, textStatus, jQxhr) {
        if (data.errorCode != 0) {
        } else {
          if (data.data != null) {
            data.data.forEach(element => {
              var date = new Date(element.millisecond);

              var m = moment(
                date.getUTCFullYear() +
                  "/" +
                  (date.getUTCMonth() + 1) +
                  "/" +
                  date.getUTCDate(),
                "YYYY/M/D"
              );

              var month = "";
              switch (m.jMonth()) {
                case 0:
                  month = "";
                  break;
                case 1:
                  month = "";
                  break;
                case 2:
                  month = "";
                  break;
                case 3:
                  month = "";
                  break;
                case 4:
                  month = "";
                  break;
                case 5:
                  month = "";
                  break;
                case 6:
                  month = "";
                  break;
                case 7:
                  month = "";
                  break;
                case 8:
                  month = "";
                  break;
                case 9:
                  month = "";
                  break;
                case 10:
                  month = "";
                  break;
                case 11:
                  month = "";
                  break;
                default:
                  break;
              }

              element.millisecond = latinToPersian(
                m.jDate() + " " + month + " " + m.jYear()
              );
            });
            this.setState({ comments: data.data, commentCount: data.count });
          }
        }
      }.bind(this),
      error: function(jqXhr, textStatus, errorThrown) {
        console.log(errorThrown);
      }
    });
  }

  componentDidMount() {
    this.getComments();
  }

  onCommentSubmit() {
    this.getComments();
  }

  render() {
    return (
      <div className="single-product-dec-content">
        {this.props.session.session == null ? (
          <div className="register-panel">
                  .
            <a className=" register-button" onClick={this.showLogin.bind(this)}>
              <strong> </strong>
            </a>
          </div>
        ) : (
          <Comment
            movieId={this.props.movieId}
            onCommentSubmit={this.onCommentSubmit.bind(this)}
          />
        )}
        {this.state.commentCount != 0 && (
          <div id="comments">
            <h2>
              {latinToPersian(this.state.commentCount.toString())}  
               
            </h2>
            <div className="commentlist">
              <div
                className="comment even thread-even depth-1"
                id="li-comment-21"
              >
                {this.state.comments.map(comment => (
                  <div
                    key={comment.id}
                    id="comment-21"
                    className="comment_container"
                  >
                    <img
                      alt=""
                      src="http://2.gravatar.com/avatar/8271ab4520301e6c326e4935bb0c66c7?s=60&amp;d=mm&amp;r=g"
                      className="avatar avatar-60 photo"
                      width="60"
                      height="60"
                    />
                    <div className="comment-text">
                      <p className="meta">
                        <strong>{comment.name}</strong> <span> / </span>
                        <span className="comment-date">
                          {comment.millisecond}{" "}
                        </span>
                      </p>
                      <div className="description">
                        <p>{comment.text}</p>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    );
  }
}



